strong, even a small sip made my face twisted. Makgeolli tastes much more smoother than soju. Kwangho introunced the traditional way to drink makgeolli. Always use both your hand to carry the bottle when pourting wine for others. Thank you Kwangho for such a nice Korean dinner!! We chatted and listened to music till 9pm. What a wonderful day with nature and Korean culture.\n\t\t\t")])])])}]

