var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"content",staticStyle:{"display":"none"},attrs:{"id":"sg-week-05"}},[_c('div',{staticClass:"content__title"},[_vm._v("\n\t\t\tNight Cycling"),_c('img',{attrs:{"src":require("../../assets/images/vertical_bars_sg.png")}})]),_c('div',{staticClass:"content__date"},[_vm._v("\n\t\t\t2019/02/18 ~ 2019/02/24\n\t\t")]),_c('div',{staticClass:"content__content"},[_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tBefore I start writing about the things happened this week, I have to tell you this first. "),_c('span',{staticStyle:{"font-weight":"bold"}},[_vm._v("The movie tickets in Singapore are SOOO CHEAP!!!")]),_vm._v(" I went to Golden Village at Paya Lebar to see the movie \"Serenity\" on Thursday, and it only cost me SGD 8.5, which is 2 dollars cheaper than Taiwan (in SGD)!! I wasn't there when the student account is available, but still, the ticket is cheap as hell! I might be a little bit exaggerated, but as a filmaholic like me, this is definitely a wonderful news. Next time I will know what to do when I feel boring. 🎬🎬🎬\n\t\t\t\t"),_c('br'),_c('br'),_vm._v("\n\t\t\t\tNow let's talk about this week starting with the Rice Balls Cooking Event! This Tuesday was Lantern Festival, which is a Chinese festival celebrated on the fifteenth day of the first month in the lunisolar. So I decided to invite some friends to cook rice balls in the hostel kitchen together. We bought three flavors: peanut, sesame and red bean. We cooked the rice balls in the boiled water and add brown sugar syrup with ginger flavor. The rice balls were really good! Although it was a little bit weird to celebrate Lantern Festival in such a hot weather, the tasty sweet rice balls did successful add on some holiday spirit.\n\t\t\t\t"),_c('br'),_c('br'),_vm._v("\n\t\t\t\tNext two days were my local-food-tasting days. I had Yong Tau Foo (釀豆腐) at Tampines on Wednesday. Yong tau foo is a Hakka Chinese cuisine consisting primarily of tofu filled with ground meat mixture or fish paste. There were also lots of vegetables to choose from. It's definitely on of my top three local food.\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week05-1"}},[_c('img',{attrs:{"src":require("../../assets/images/week05/taufoo.jpg")}})])]),_c('div',{staticClass:"horizontal-single__annotation"},[_vm._v("Variation of 釀豆腐 include vegetables and mushrooms stuffed with ground meat or surimi. It can be eaten in numerous ways, either dry with a sauce or served as a soup dish.")])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tI went to Old Airport Road Food Court on Thursday, which is recommended by a local student. Old Airport Road FC has a great variety of food to choose from, and the price is really reasonable. I had mango-dragonfruit juice, hokkien fried mee (福建蝦麵) and Cheong Fun (腸粉). The interesting thing about hokkien fried mee is that it's wrapped in paper! I think I will visit there again soon, and maybe go see a movie after that as well.\n\t\t\t")]),_c('div',{staticClass:"content__content--image"},[_c('div',{staticClass:"desktop-img mixed-triple"},[_c('div',{staticClass:"inline-block"},[_c('div',{staticClass:"mixed-triple__frame1 img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week05-2"}},[_c('img',{attrs:{"src":require("../../assets/images/week05/drink.jpg")}})])])]),_c('div',{staticClass:"inline-block"},[_c('div',{staticClass:"mixed-triple__frame2 img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week05-2"}},[_c('img',{attrs:{"src":require("../../assets/images/week05/mee.jpg")}})])]),_c('div',{staticClass:"mixed-triple__frame3 img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week05-2"}},[_c('img',{attrs:{"src":require("../../assets/images/week05/fun.jpg")}})])])]),_c('div',{staticClass:"mixed-triple__annotation"},[_vm._v("1) The color of the drink was beautiful and taste super good. 2) There were many shrimps in it! Definitely worth the price. 3) 叉燒腸粉 and 雞蛋腸粉.")])]),_c('div',{staticClass:"mobile-img swiper-container"},[_c('div',{staticClass:"swiper-wrapper"},[_c('div',{staticClass:"swiper-slide"},[_c('img',{attrs:{"src":require("../../assets/images/week05/mee.jpg")}})]),_c('div',{staticClass:"swiper-slide"},[_c('img',{attrs:{"src":require("../../assets/images/week05/fun.jpg")}})])]),_c('div',{staticClass:"swiper-button-next"}),_c('div',{staticClass:"swiper-button-prev"})])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tFinally, there comes the most exciting event of the week! The Night Cycling! We started from Singapore Sports Hub (National Stadium), via Gardens by the Bay, MBS, had a meal at East Coast Park, and rode back to National Stadium. We started at around 10 pm and finished at 3:30 am. The route is about 32 kilometers long, which is quite relaxing for a 4-hour ride. The city still looks very beatutiful at night and I made friends with some local students of SUTD and also some foreign students from others schools. After the event, I decided to ride a bike to Johor Bahru, which is a city of Malaysia that is connected to Singapore. And maybe cycling around the Singapore island. I'm so escited about it!\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week05-3"}},[_c('img',{attrs:{"src":require("../../assets/images/week05/cycling.jpg")}})])]),_c('div',{staticClass:"horizontal-single__annotation"},[_vm._v("Groupd photo of some participants of Night Cycling.")])])])])}]

