nce since it's the most colorful shaved ice I've ever tasted!\n\t\t\t")]),_c('div',{staticClass:"content__content--image"},[_c('div',{staticClass:"desktop-img horizontal-double"},[_c('div',{staticClass:"horizontal-double__frame1 inline-block img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week01-1"}},[_c('img',{attrs:{"src":require("../../assets/images/week01/chinatown.jpg")}})])]),_c('div',{staticClass:"horizontal-double__frame2 inline-block img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week01-1"}},[_c('img',{attrs:{"src":require("../../assets/images/week01/teamates.jpg")}})])]),_c('div',{staticClass:"horizontal-double__annotation"},[_vm._v("1) The view from Chinatown Foodcourt. 2) My Amazing Race teammates.")])]),_c('div',{staticClass:"mobile-img swiper-container"},[_c('div',{staticClass:"swiper-wrapper"},[_c('div',{staticClass:"swiper-slide"},[_c('img',{attrs:{"src":require("../../assets/images/week01/chinatown.jpg")}})]),_c('div',{staticClass:"swiper-slide"},[_c('img',{attrs:{"src":require("../../assets/images/week01/teamates.jpg")}})])]),_c('div',{staticClass:"swiper-button-next"}),_c('div',{staticClass:"swiper-button-prev"})])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tNext, we went to habourfront station. We visited helix bridge, the shoppes at Marina Bay Sands, and Garden by the Bay. The Marina Bay Sands is really spectacular when watching it closely, and the garden is also very splendid. It's like the reproduce of the scene in the movie \"Crazy Rich Asians\". Finally we took MRT to Dhoby Ghaut station, and visited Singapore Art Museum, National Museum of Singapore, Chijmas, St. Andrews Cathedral, and finally the MacDonald House, which was the first building with air-conditioner in Southeast Asia. We took lots of great pictures and got to know Singaporean history and environment more.\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame 