round ferry. Walking in the Palau Ubin Island is like walking in a primeval forest. The trees are tall that they almost cover the sky. Although there was a super heavy rain that day, me and Joyce still enjoy the natural vibe a lot. Now I'm kind of regret of not visiting there earlier! \n\t\t\t")]),_c('div',{staticClass:"content__content--text"},[_c('p',{staticStyle:{"font-weight":"bold"}},[_vm._v("3. Last day in Singapore")]),_vm._v("\n\t\t\t\tHere it comes the last day of my exchange journey! I packed all my stuffs into two big suit cases, just like that, a room full of 4-month of memories are now empty. I missed my friends, my families, my home, but I was still a little bit reluctant to leave. Thank you SUTD, thank you TFI LEaRN Programme, and thank you Singapore for giving me my most adventurous time of my life!\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week15-3"}},[_c('img',{attrs:{"src":require("../../assets/images/week15/SUTD.jpg")}})])]),_c('div',{staticClass:"horizontal-single__annotation"},[_vm._v("Singapore University of Technology and Design.")])])])])}]

