he Palawan beach, which is the southermost point of continental Asia! However, there were some garbages left on the beach, so my dad and mom spent some time to gather them. Despite this, the beach was still very beautiful. The sands were soft and the water was cool. The cloudless sky was blue and connected with the boundless sea. That is definitely a view that I will keep in my mind.\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week02-5"}},[_c('img',{attrs:{"src":require("../../assets/images/week02/bridge.jpg")}})])]),_c('div',{staticClass:"horizontal-single__annotation"},[_vm._v("The drawbridge on Palawan Beach.")])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tI know that when I'm thinking about them in the night, they're also thinking about me. The distance between us doesn't seem that long anymore.\n\t\t\t")])])])}]

