 on the chicken was extremely good and the spicy sauce served with the meal was super hot. I bursted into tears the second I tasted it.\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week03-1"}},[_c('img',{attrs:{"src":require("../../assets/images/week03/nasi_lemak.jpg")}})])]),_c('div',{staticClass:"horizontal-single__annotation"},[_vm._v("Eating Nasi Lemak with Lisa's families in the night market near her home.")])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tNext day, I woke up with excimentment, looking forward to the attractions we were going to visit that day. I was really lucky because Lisa's aunt would drive us around the city.\n\t\t\t\t"),_c('br'),_c('br'),_vm._v("\n\t\t\t\tThe first stop is Istana Negara, the National Palace of Malaysia. The building is very majestic. The surface of the palace was covered with some golden paint. It shined under the couldless blue sky, made the palace even more noble.\n\t\t\t")]),_c('div',{staticClass:"content__content--image horizontal-single"},[_c('div',{staticClass:"horizontal-single__frame img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week03-2"}},[_c('img',{attrs:{"src":require("../../assets/images/week03/palace.jpg")}})])]),_c('div',{staticClass:"horizontal-single__annotation"},[_vm._v("The gate of the National Palace. There was one guard riding on the horse at each side of the gate.")])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tAfter lunch, we went to Central Market and Petaling Street. The stores in Central Market sell traditional handicrafts, clothing and artworks. We met an artist who made 捕夢網. He was very hospitable and tought us how to make one. He said that it took him years of practicing and thousands of pieces to make him a master. The 捕夢網 he made were very beautiful and exquisite. The last stop was the famous skyscraper KLCC. Unfortunately, the weather was bad in the evening so I couldn't take a good picture of KLCC. But we had delicious local desserts at Nyonya Colors at KLCC. A good dessert can always make the day.\n\t\t\t")]),_c('div',{staticClass:"content__content--image"},[_c('div',{staticClass:"desktop-img horizontal-double"},[_c('div',{staticClass:"horizontal-double__frame1 inline-block img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week03-3"}},[_c('img',{attrs:{"src":require("../../assets/images/week03/bowls.jpg")}})])]),_c('div',{staticClass:"horizontal-double__frame2 inline-block img_lightbox"},[_c('a',{attrs:{"href":"","data-lightbox":"week03-3"}},[_c('img',{attrs:{"src":require("../../assets/images/week03/nyonya.jpg")}})])]),_c('div',{staticClass:"horizontal-double__annotation"},[_vm._v("1) The bowls made of coconut shell from Central Market. 2) The colorful nyonya desserts. They look good and taste even better!")])]),_c('div',{staticClass:"mobile-img swiper-container"},[_c('div',{staticClass:"swiper-wrapper"},[_c('div',{staticClass:"swiper-slide"},[_c('img',{attrs:{"src":require("../../assets/images/week03/bowls.jpg")}})]),_c('div',{staticClass:"swiper-slide"},[_c('img',{attrs:{"src":require("../../assets/images/week03/nyonya.jpg")}})])]),_c('div',{staticClass:"swiper-button-next"}),_c('div',{staticClass:"swiper-button-prev"})])]),_c('div',{staticClass:"content__content--text"},[_vm._v("\n\t\t\t\tThe next day is even more exciting! We had 辣椒板麵 as breakfast. The noodle tasted super good with the half-cooked egg and paprika in it. After breakfast, we headed to Batu Caves, a limestone hill that has a series of caves and cave temples. The stairs to the caves are colorful and there were many monkeys around the caves. The Buddha statue standing at the entrance of the cave is very tall and solemn.\n\t\t\t")]),_c('div',{staticClass:"content__content--image"},[_c('div',{staticClass:"desktop-img mixed-triple"},[_c('div',{staticClass:"inline-block"},[_c('div'